* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 0;
	margin: 0;
}
html {
	height: 100%;
}
body {
	height: 100%;
}
button {
	border: none;
	cursor: pointer;
}
.root {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}
.gameScreen {
	width: 300px;
	height: 300px;
	font-size: 350%;
	font-family: 'Raleway', sans-serif;
	font-weight: bold;
	-webkit-animation: showGameScreen  1s;
	        animation: showGameScreen  1s;
}
.gameScreen button {
	background: LightGrey;
	border: 1px solid LightSlateGray;
}
.gameScreen button:hover {
	background: LightSteelBlue;
}
.startGameButton {
	width: 100%;
	height: 30%;
	font-size: 70%;
	font-family: 'Raleway', sans-serif;
	font-weight: bold;
}
.comment {
	width: 100%;
	height: 20%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 32%;
	border: 1px solid LightSlateGray;
}
.idBox {
	width: 100%;
	height: 20%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 50%;
	font-family: 'Raleway', sans-serif;
	font-weight: bold;
}
.joinGameButton {
	width: 100%;
	height: 30%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 70%;
	font-family: 'Raleway', sans-serif;
	font-weight: bold;
}
.waitingComment {
	width: 100%;
	height: 50%;
	font-size: 50%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;
	border: 1px solid LightSlateGray;
	-webkit-animation: showGameScreen  1s;
	        animation: showGameScreen  1s;
}
.waitingInput {
	width: 100%;
	height: 50%;
	font-size: 200%;
	text-align: center;
}
.gameBoard {
	width: 100%;
	height: 100%;
	position: relative;
}
.gameBox {
	width: 33.333%;
	height: 33.333%;
	float: left;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
	font-size: 60px;
}
.showMove {
	-webkit-animation: showGameBoxFontSize  0.5s;
	        animation: showGameBoxFontSize  0.5s;
}
.warning {
	background-color: MistyRose;
	font-size: 25px;
	padding: 5px;
	border: 1px solid Tomato;
	height: 60px;
	border-radius: 5px;
	margin-top: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-animation: showWarning  2s;
	        animation: showWarning  2s;
}
.none {
	display: none;
}
.win {
	background: PaleGreen !important;
	width: 33.333%;
	height: 33.333%;
	float: left;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
	font-size: 100%;
}
.waitingForOpponent {
	position: absolute;
	width: 100%;
	height: 100%;
	background: rgba(245,245,220, 0.5);
	font-size: 40%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-shadow: 2px 2px 3px white;
	color: Chocolate;
	-webkit-animation: showWaitingForOpponent  0.5s;
	        animation: showWaitingForOpponent  0.5s;
}
@-webkit-keyframes showGameBoxFontSize {
	0% {
		font-size: 40px;
	}
	100% {
		font-size: 60px;
	}
}
@keyframes showGameBoxFontSize {
	0% {
		font-size: 40px;
	}
	100% {
		font-size: 60px;
	}
}
@-webkit-keyframes showGameScreen {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes showGameScreen {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@-webkit-keyframes showWaitingForOpponent {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes showWaitingForOpponent {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@-webkit-keyframes showWarning {
	0% {
		font-size: 0;
		height: 0;
	}
	35% {
		font-size: 25px;
		height: 60px;
	}
	80% {
		font-size: 25px;
		height: 60px;
	}
	100% {
		font-size: 0;
		height: 0;
	}
}
@keyframes showWarning {
	0% {
		font-size: 0;
		height: 0;
	}
	35% {
		font-size: 25px;
		height: 60px;
	}
	80% {
		font-size: 25px;
		height: 60px;
	}
	100% {
		font-size: 0;
		height: 0;
	}
}


